import React from 'react';
import InputImpl from './InputImpl';
import InputAddonWrapper from './InputAddonWrapper';

interface DSInputProps {
  containerProps?:Object;
  id?: string,
  autoFocus?: boolean,
  disabled?: boolean,
  className?: string,
  clearable?: boolean,
  name?: string,
  maxLength?: number,
  minLength?: number,
  fluidWidth?: boolean,
  disableTooltip?: boolean,
  placeholder?: string,
  value?: string | number,
  hasError?: boolean,
  readOnly?: boolean,
  innerRef?: React.RefObject<unknown>,
  type?: string,
  min?: number,
  max?: number,
  leftComponent?: JSX.Element,
  rightComponent?: JSX.Element,
  required?: boolean,
  customInputType?: React.Component,
  variant?: string,
  isShowElipsisActive?: boolean,  
}

const DSInput = ({
  containerProps = {},
  id,
  autoFocus = false,
  disabled = false,
  className = '',
  clearable = false,
  name = '',
  maxLength = 255,
  minLength = 0,
  fluidWidth = false,
  disableTooltip = true,
  placeholder,
  value = '',
  hasError = false,
  readOnly = false,
  innerRef = null,
  type = 'text',
  min = undefined,
  max = undefined,
  leftComponent = null,
  rightComponent = null,
  required = false,
  customInputType = null,
  variant,
  isShowElipsisActive = true,
  ...otherProps
}: DSInputProps) => (
  <InputImpl
    {...otherProps}
    aria-label={otherProps['aria-label']}
    autoFocus={autoFocus}
    className={`${className} ${disabled ? 'input-disabled' : ''}`}
    clearable={clearable}
    containerProps={containerProps}
    customInputType={customInputType}
    data-testid={otherProps['data-testid'] || 'ds-input_main-component'}
    disabled={disabled}
    disableTooltip={disableTooltip}
    fluidWidth={fluidWidth}
    hasError={hasError}
    id={id}
    innerRef={innerRef}
    isShowElipsisActive={isShowElipsisActive}
    leftComponent={leftComponent}
    max={max}
    maxLength={maxLength}
    min={min}
    minLength={minLength}
    name={name}
    placeholder={placeholder}
    readOnly={readOnly}
    required={required}
    rightComponent={rightComponent}
    type={type}
    value={value}
    variant={variant}
  />
);

export { InputAddonWrapper };

export default DSInput;
